<template>
  <b-row>
    <b-col>
      <b-container fluid>
       <b-row class="mt-5 mb-5">
        <b-col md="2" cols="auto"></b-col>
        <b-col md="8" cols="12">
          <b-form>
            <b-row>
              <b-col cols="12" md="4">
                <b-form-group
                    id="input-group-1"
                    label=" "
                    label-for="input-1"
                    label-align="left"
                >
                  <b-form-input
                      id="input-1"
                      v-model="form.code"
                      type="text"
                      size="lg"
                      disabled
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col  cols="12" md="4">
                    <b-form-group label="  " v-slot="{ ariaDescribedby }">
                        <b-form-radio v-model="form.person" :aria-describedby="ariaDescribedby" name="some-radios" :value="1"> </b-form-radio>
                        <b-form-radio v-model="form.person" :aria-describedby="ariaDescribedby" name="some-radios" :value="2"> </b-form-radio>
                    </b-form-group>
                </b-col>
                <b-col cols="12" md="4">
                <b-form-group
                    id="input-group-1"
                    label=""
                    label-for="input-1"
                    label-align="left"
                >
                  <b-form-input
                      id="input-1"
                      v-model="form.coefiction"
                      type="number"
                      size="lg"
                      :min='1'
                      :max='2'
                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
            <b-form-group
                    id="input-group-2"
                    label=" "
                    label-for="input-2"
                    label-align="left"
                >
                  <b-form-select
                      id="input-2"
                      v-model="form.type"
                      :options="types_work"
                      size="lg"

                  ></b-form-select>
            </b-form-group>
            <b-row>
                <b-col cols="12" md="4">
                <b-form-group
                    id="input-group-10"
                    label=""
                    label-for="input-10"
                    label-align="left"
                >
                  <b-form-input
                      id="input-10"
                      v-model="form.sum"
                      type="number"
                      size="lg"
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="12" md="4">
                <b-form-group
                    id="input-group-11"
                    label=" "
                    label-for="input-11"
                    label-align="left"
                >
                  <b-form-input
                      id="input-11"
                      v-model="form.copyes"
                      type="number"
                      size="lg"
                      :min='0'
                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="12" md="4">
                <b-form-group
                    id="input-group-11"
                    label="   "
                    label-for="input-11"
                    label-align="left"
                >
                  <b-form-input
                      id="input-11"
                      v-model="form.additionally"
                      type="number"
                      size="lg"
                      :min='0'
                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>

            <b-row>
                <b-col>
                    <b-form-group
                    id="input-group-12"
                    label=""
                    label-for="input-12"
                    label-align="left"
                >
                  <b-form-input
                      id="input-12"
                      v-model="form.city"
                      type="text"
                      placeholder="  "
                      size="lg"
                  ></b-form-input>
                </b-form-group>
                </b-col>
            </b-row>
            <b-row>
              <b-col>
                  <b-form-textarea
                    id="textarea"
                    v-model="form.address"
                    placeholder="..."
                    rows="3"
                    size="lg"
                    max-rows="6"
                ></b-form-textarea>
              </b-col>
              <b-col>
                <b-form-textarea
                    id="textarea"
                    v-model="form.personal_data"
                    placeholder=" /  "
                    rows="3"
                    size="lg"
                    max-rows="6"
                ></b-form-textarea>
              </b-col>
            </b-row>
            <b-row class="mt-3">
              <b-col cols="4">
                <b-form-group
                    id="input-group-4"
                    label=" "
                    label-for="input-4"
                    label-align="left"
                >
                  <b-form-input
                      id="input-4"
                      v-model="form.house_number"
                      type="text"
                      placeholder="  "
                      size="lg"

                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="4">
                <b-form-group
                    id="input-group-6"
                    label=" "
                    label-for="input-6"
                    label-align="left"
                >
                  <b-form-input
                      id="input-6"
                      v-model="form.house_building"
                      type="text"
                      placeholder="  "
                      size="lg"

                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="4">
                <b-form-group
                    id="input-group-5"
                    label=" "
                    label-for="input-5"
                    label-align="left"
                >
                  <b-form-input
                      id="input-5"
                      v-model="form.apartment"
                      type="text"
                      placeholder="  "
                      size="lg"

                  ></b-form-input>
                </b-form-group>
              </b-col>
              <b-col cols="4">
                <b-form-group
                    id="input-group-5"
                    label=" "
                    label-for="input-5"
                    label-align="left"
                >
                  <b-form-input
                      id="input-5"
                      v-model="form.office"
                      type="text"
                      placeholder="  "
                      size="lg"

                  ></b-form-input>
                </b-form-group>
              </b-col>
            </b-row>
            <b-row class="mt-3 mb-3">
              <b-col>
                <b-button type="button" @click="createOrder()" variant="primary" size="lg" class="mr-3"> </b-button>
                <b-button type="button" @click="saveOrder()" variant="primary" size="lg" class="mr-3"></b-button>
                <b-button type="button" @click="blockOrder()" variant="danger" size="lg" ></b-button>
              </b-col>
            </b-row>
            <b-row>
              <b-col>
                <b-link :href="`admin/proposals/find_for_pdf/${getProposalId}`" class="mr-3 link" target="_blank"></b-link>
                <b-link @click.prevent="saveOrder(true)" class="mr-3 link"> </b-link>
              </b-col>
            </b-row>
          </b-form>
        </b-col>
        <b-col md="2" cols="auto"></b-col>
      </b-row>
      </b-container>
    </b-col>
  </b-row>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

const TYPE_WORK_SUM = [
        {
          id: 1,
          sum: 495.48
        },
        {
          id: 2,
          sum: 434.69
        },
        {
          id: 4,
          sum: 438.32
        },
        {
          id: 5,
          sum: 377.53
        },
        {
          id: 6,
          sum: 426.85
        },
        {
          id: 7,
          sum: 366.06
        },
        {
          id: 9,
          sum: 438.32
        },
        {
          id: 10,
          sum: 262.12
        },
        {
          id: 11,
          sum: 331.48
        },
      ];


export default {
  name: 'ProposalsForm',
  data() {
    return {
      form: {
        code: '',
        sum: null,
        copyes: 0,
        type: null,
        person: 1,
        personal_data: null,
        address: null,
        city: null,
        house_number: null,
        house_building: null,
        apartment: null,
        office: null,
        status: 0,
        pages: 0,
        coefiction: 1,
        additionally: 0,
      },
      types_work: [
        {text: '  ', value: null},
        {text: '1.         ', value: 1},
        {text: '2.         ', value: 2},
        {text: '4.      \'    ', value: 4},
        {text: '5.      \'    ', value: 5},
        {text: '6.          ', value: 6},
        {text: '7.          ', value: 7},
        {text: '9.     ', value: 9},
        {text: '10.         ', value: 10},
        {text: '11.         ', value: 11},
        {text: '12.    (  )   ', value: 12},
        {text: '13.    (  )   ', value: 13},
        {text: '14.         (,     )', value: 14},
        {text: '15.        (,     )', value: 15},
        {text: '16.         (,     )', value: 16},
        {text: '17.         (,     )', value: 17},
        {text: '18.         (,   )', value: 18},
        {text: '19.         (,   )', value: 19},
        {text: '20.         (,   )', value: 20},
        {text: '21.         (,   )', value: 21},
        {text: '22.    ,    ', value: 22},
        {text: '23.        ', value: 23},
        {text: '24.     ,  ,  / , / /        ', value: 24},
        {text: '25.      ', value: 25},
        {text: '26.         ', value: 26},
        {text: '27.         ', value: 27},
      ],
      total: 0,
      sumCopyes: 0.91,
      dataLoaded: false,
    }
  },
  props: {
    current_invent: {
      type: Number
    }
  },
  watch: {
      'form.type': {
        immediate: false,
        handler(newVal) {
          if(!this.dataLoaded) {
            this.form.sum = TYPE_WORK_SUM.find(item => item.id === newVal).sum;
          }
        }

      },
      'form.copyes': {
        immediate: false,
        handler(newVal, oldVal) {
         if(newVal != oldVal && !this.dataLoaded) {
              if(newVal>oldVal) {
                this.form.sum = parseFloat((this.form.sum + this.sumCopyes).toFixed(10));
              } else {
                this.form.sum = parseFloat((this.form.sum - this.sumCopyes).toFixed(10));
              }
          }
        }

      },
      'form.coefiction': {
        immediate: false,
        handler(newVal, oldVal) {
          if(newVal != oldVal && !this.dataLoaded) {
              if(newVal>oldVal) {
                this.form.sum = this.form.sum * 2;
              } else {
                this.form.sum = this.form.sum / 2;
              }
          }
        }

      },
      'form.additionally': {
        immediate: false,
        handler(newVal, oldVal) {
          if(newVal != oldVal && !this.dataLoaded) {
              if(newVal>oldVal) {
                this.form.sum = this.form.sum + 20.75;
              } else {
                this.form.sum = this.form.sum - 20.75;
              }
          }
        }

      },
  },
  computed: {
    ...mapGetters(['currentProposalsData', 'lastProposalsId']),
    getProposalId() {
      return this.currentProposalsData && this.currentProposalsData?.id;
    },
    getCurrentLastSum() {
      return this.currentProposalsData && this.currentProposalsData?.sum
    },
  },
  methods: {
    ...mapActions(['createProposal', 'getCurrentProposal', 'saveProposal', 'findLastProposal', 'generateProposalPDF']),
    createOrder() {
      const formData = new FormData();
      const id = this.current_invent;
      this.form.status = 0;

      Object.entries(this.form).map(item => {
        formData.append(item[0], item[1]);
      })

      if(id) {
        this.saveProposal({data: this.form, id}).then(()=> {
          this.backToTable();
        })
      } else {
        this.createProposal(this.form).then(()=> {
          this.backToTable();
        })
      }

    },
    saveOrder(isSurcharge) {
      if(!isSurcharge) {
        this.form.status = 1;
      }

      const formData = new FormData();
      const id = this.current_invent;

      Object.entries(this.form).map(item => {
        formData.append(item[0], item[1]);
      })

      if(id) {
        this.saveProposal({data: this.form, id}).then(()=> {
          if(isSurcharge) {
            window.open(`${document.location}/proposals/find_for_pdf/${this.getProposalId}?sum=${localStorage.getItem('lasSum')}`)
          } else {
            this.backToTable();
          }
        })
      } else {
        this.createOrder();
      }
    },
    blockOrder() {
      this.form.status = 2;

      const id = this.current_invent;
      const formData = new FormData();

      Object.entries(this.form).map(item => {
        formData.append(item[0], item[1]);
      })

      if(id) {
        this.saveProposal({data: this.form, id}).then(()=> {
          this.backToTable();
        })
      } else {
        this.createOrder();
      }
    },
    fulledData(id) {
      this.getCurrentProposal(id).then(response => {
        this.form = response;
        this.dataLoaded = true;

        setTimeout(() => {
          this.dataLoaded = false;
          localStorage.setItem('lasSum', this.getCurrentLastSum);
        }, 1000)
      })
    },
    backToTable() {
      this.$emit('componentChange', 'ProposalsTable');
    },
    getCurrentCode() {
      let id = +this.lastProposalsId;

      if(id < 10) {
          this.form.code = `00000000${id}`
      } else if (id >= 10 && id < 100) {
          this.form.code = `0000000${id}`
      } else if (id >= 100 && id < 1000) {
          this.form.code = `0000000${id}`
      } else if (id >= 1000 && id < 10000) {
          this.form.code = `000000${id}`
      } else if (id >= 10000 && id < 100000) {
          this.form.code = `00000${id}`
      } else if (id >= 100000 && id < 1000000) {
          this.form.code = `0000${id}`
      } else if (id >= 1000000 && id < 10000000) {
          this.form.code = `000${id}`
      } else if (id >= 10000000 && id < 100000000) {
          this.form.code = `00${id}`
      } else if (id >= 100000000 && id < 1000000000) {
          this.form.code = `0${id}`
      }
    },
    printOrder(id) {
        this.generateProposalPDF(id)
    },
  },
  mounted() {
    if(this.current_invent) {
      this.fulledData(this.current_invent);


    } else {
      this.findLastProposal().then(() => {
        this.getCurrentCode();
      });
    }
  },
  beforeDestroy() {
    localStorage.setItem('lasSum', 0);
    this.$emit('chooseCurrent',null);
  }
}
</script>

<style lang="scss">

  .link {
    padding: 0.5rem 1rem;
    font-size: 1.25rem;
    line-height: 1.5;
    border-radius: 0.3rem;
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;

    &:hover {
      color: #fff;
      background-color: #0069d9;
      border-color: #0062cc;
      text-decoration: none
    }
  }
</style>
