<template>
  <b-row>
    <b-col>
      <b-container>
        <b-row class="mt-5">
          <b-col cols="12" md="6">
            <b-form @submit.prevent="onSubmit">
              <b-form-group
                  id="input-group-1"
                  label=" "
                  label-for="input-1"
              >
                <b-form-input
                    id="input-1"
                    v-model="form.name"
                    type="text"
                    placeholder=" "
                    required
                ></b-form-input>
              </b-form-group>

              <b-form-group id="input-group-2" label=" " label-for="input-2">
                <b-form-input
                    id="input-2"
                    v-model="form.password"
                    placeholder=" "
                    required
                ></b-form-input>
              </b-form-group>

              <b-form-group id="input-group-3" label="  " label-for="input-3">
                <b-form-select
                    id="input-3"
                    v-model="form.role"
                    :options="roles"
                    required
                ></b-form-select>
              </b-form-group>

              <b-button type="submit" variant="danger"> </b-button>
              <p class="red mt-3" v-if="error"> .  </p>
              <p class="red mt-3" v-if="success">  </p>
            </b-form>
          </b-col>
        </b-row>
      </b-container>
    </b-col>
  </b-row>
</template>

<script>
import { mapActions } from 'vuex';
export default {
  name: 'AddUser',
  data() {
    return {
      form: {
        name: '',
        password: '',
        email: '',
        role: 0
      },
      error: false,
      success: false,
      roles: [
          {
            text: ' ',
            value: 0
          },
          {
            text: ' ',
            value: 1
          },
          {
            text: '   ',
            value: 2
          },
          {
            text: '  ',
            value: 3
          },
      ],
    }
  },
  methods: {
    ...mapActions(['registration']),
    onSubmit() {
      const formData = new FormData();
      this.error = false;
      this.success = false;

      Object.entries(this.form).map(item => {
        formData.append(item[0], item[1]);
      })

      this.registration(formData).then(()=> {
        this.success = true;
      }).catch(() => {
        this.error = true;
      });

    }
  }
}
</script>

