<template>
  <b-row>
    <b-col>
      <b-container fluid>
        <b-row class="mt-5 mb-2" align-v="center">
          <b-col cols="12" md="3">
            <b-form>
              <b-form-group
              >
                <b-form-input
                    id="input-1"
                    v-model="search"
                    type="text"
                    placeholder="..."
                    required
                ></b-form-input>
              </b-form-group>
            </b-form>
          </b-col>
          <b-col cols="12" md="5">
            <b-form-group label=" :">
              <b-form-checkbox-group
                id="checkbox-group-1"
                v-model="selected"
                :options="options"
                name="flavour-1"
              ></b-form-checkbox-group>
            </b-form-group>
          </b-col>
          <b-col cols="12"  md="4" align="center">
            <b-button type="button" variant="danger" size="lg" @click="$emit('componentChange', 'ProposalsForm')"></b-button>
          </b-col>
        </b-row>
        <b-row class="mb-4">
          <b-col>
            <a href="#">
            <download-excel
                :data="setAllDataTable"
                :fields="json_fields"
                name="filename.xls">

                 Excel
              <img src="https://img.icons8.com/officexs/16/000000/ms-excel.png" class="mt-n1 ml-2"/>
            </download-excel>
            </a>
          </b-col>
        </b-row>
        <template v-if="setAllDataTable.length">
          <b-row>
            <b-col>
              <b-table striped
                       hover
                       responsive
                       small
                       :items="setAllDataTable"
                       :fields="fields"
                       :current-page="currentPage"
                       :per-page="perPage"
                       :filter="search"
                       :filter-ignored-fields="ignoredFields"
                       :filter-included-fields="selected"
                       :filter-debounce="200"
                       @filtered="onFiltered"
                       @row-clicked="(item) => openToEdit(item)"
                       class="admin__table">
                <template v-slot:cell(person)="row">
                  <span v-if="row.item.person === 1"></span>
                  <span v-if="row.item.person === 2"></span>
                </template>
                <template v-slot:cell(status)="row">
                  <span class="yellow" v-if="row.item.status === 0"></span>
                  <span class="green" v-if="row.item.status === 1"></span>
                  <span class="red" v-if="row.item.status === 2"></span>
                </template>
              </b-table>
            </b-col>
          </b-row>
          <b-row class="mt-3 mb-5">
            <b-col>
              <b-pagination
                  v-model="currentPage"
                  :total-rows="totalRows"
                  :per-page="perPage"
                  pills
              ></b-pagination>
            </b-col>
          </b-row>
        </template>
        <template v-else>
          <b-row>
            <b-col align="center">
              <p class="red"> </p>
            </b-col>
          </b-row>
        </template>
      </b-container>
    </b-col>
  </b-row>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: 'ProposalsTable',
  data() {
    return {
      search: null,
      currentPage: 1,
      perPage: 40,
      totalRows: null,
      json_fields: {
        ' ': 'code',
        '': 'sum',
        '': 'copyes',
        ' ': 'type',
        './. ': 'person',
        ' ': 'personal_data',
        '': 'city',
        '': 'address',
        ' .': 'house_number',
        '': 'house_building',
        ' .': 'apartment',
        ' ': 'office',
        '': 'status',
        '': 'coefiction',
        ' ': 'created_at',
        ' ': 'updated_at',
      },
      fields: [
        {
          key: "code",
          label: " ",
        },
        {
          key: "sum",
          label: "",
          sortable: true
        },
        {
          key: "copyes",
          label: "",
          sortable: true
        },
        {
          key: "type",
          label: " ",
          sortable: true
        },
        {
          key: "person",
          label: "./. ",
          sortable: true
        },
        {
          key: "personal_data",
          label: " /  ",
          sortable: true
        },
        {
          key: "city",
          label: "",
          sortable: true
        },
        {
          key: "address",
          label: "",
          sortable: true
        },
        {
          key: "house_number",
          label: " ",
          sortable: true
        },
        {
          key: "house_building",
          label: "",
          sortable: true
        },
        {
          key: "apartment",
          label: " ",
          sortable: true
        },
        {
          key: "office",
          label: " ",
          sortable: true
        },
        {
          key: "status",
          label: "",
          sortable: true
        },
        {
          key: "coefiction",
          label: "",
          sortable: true
        },
        {
          key: "additionally",
          label: "",
          sortable: true
        },
        {
          key: "created_at",
          label: " ",
          sortable: true
        },
        {
          key: "updated_at",
          label: " ",
          sortable: true
        },
      ],
      ignoredFields: ['created_at', 'updated_at', 'coefiction', 'status', 'type', 'copyes'],
      selected: [],
      options: [
        { text: ' ', value: 'code' },
        { text: '', value: 'sum' },
        { text: ' ', value: 'personal_data' },
        { text: '', value: 'address' }
      ]
    }
  },
  computed: {
    ...mapGetters(['proposalsData']),
    setAllDataTable() {
      return this.proposalsData.length ? this.proposalsData : [];
    },
    sortOptions() {
      return this.fields
          .filter(f => f.sortable)
          .map(f => {
            return { text: f.label, value: f.key }
          })
    }
  },
  methods: {
    ...mapActions(['getAllProposalsData']),
    openToEdit(item) {
      this.$emit('chooseCurrent', item.id);
      this.$emit('componentChange', 'ProposalsForm');
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length
      this.currentPage = 1
    }
  },
  created() {
    this.totalRows = this.proposalsData.length
  },
  mounted() {
    this.getAllProposalsData()
  }
}
</script>

<style lang="scss">
.admin {

  .page-item.active .page-link {
    background-color: #dc3545;
    border-color: #dc3545;

    &:focus {
      box-shadow: none;
    }
  }

}
</style>